.message{
    width: 100%;
    padding: 1em;
    border: 1px solid #000;
    margin: 0 auto;
    text-align: center;
    border-radius: 5px;
    margin-bottom: 15px;
}
.success{
    color: #155724;
    background-color: #D4FDDA;
    border-color: #C3E6CB;
}
.error{
    color: #721C24;
    background-color: #F8D7DA;
    border-color: #F5C6CB;
}